<template>
  <div class="list-item">
    <span class="budget-comment">{{item.comment}}</span>
    <span class="budget-value" :class="addClass">
        <i class="icon" :class="addIcon"></i>
        {{item.value}}
      </span>
    <ElButton type='danger' size='mini' @click="onClickShowDialog">Delete</ElButton>
    <template v-if="dialogVisible">
       <ElDialog
          title="Tips"
          :visible.sync="dialogVisible">
          <span>You really won't delete this item?</span>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="onConfirm">Confirm</el-button>
            <el-button @click="onCancell">Cancel</el-button>
          </span>
    </ElDialog>
    </template>
  </div>
</template>

<script>


export default {
  name: 'BudgetListItem',
  components: {

  },
  props: {
    item: {
      type: Object,
      default: () => ({})
    }
  },
  data: () => ({
    dialogVisible: false,
    confirm: null
  }),
  methods: {
    onClickShowDialog () {
      this.dialogVisible = true
    },
    onConfirm(id) {
      this.confirm = true
      this.$emit('onDelete', id)
      this.dialogVisible = false
    },
    onCancell() {
      this.confirm = false
      this.dialogVisible = false
    },
  },
  computed: {
    addClass() {
      return this.item.type === 'INCOME' ? 'green' : 'red'
    },
    addIcon() {
      return this.item.type === 'INCOME' ? 'el-icon-top' : 'el-icon-bottom'
    }
  },
}
</script>

<style scoped>
 .list-item {
    display: flex;
    align-items: center;
    padding: 10px 15px;
  }
  .budget-value {
    font-weight: bold;
    margin-left: auto;
    margin-right: 20px;
  }
  .red {
    color:red
  }
  .green {
    color: green
  }
</style>