<template>
  <div id="app">
    <Form @sumbitForm='onFormSubmit'/>
    <TotalBalance :total='totalValue' :income='incomeValue' :outcome='outcomeValue' :result='result'/>
    <div class="btn-group" @click="onButtonClick(result, $event)">
              <button type='success'  value='income' class='income' size='medium' @click="onButtonClick(result, $event)">Income</button>

              <button type='danger' value='outcome' class='outcome' size='medium' @click="onButtonClick(result, $event)">Outcome</button>

              <button type='primary'  value='all' class="all" size='medium' @click="onButtonClick(result, $event)">All</button>
    </div>
    <BudgetList :list='list' :checkedValue='result' @onDeleteItem='deleteItem'/>
  </div>
</template>

<script>
import BudgetList from './components/BudgetList'
import TotalBalance from './components/TotalBalance'
import Form from './components/Form'

export default {
  name: 'App',
  components: {
    Form,
    BudgetList,
    TotalBalance,
  },
  data: () => ({
    list: {
          1: {
      type: 'INCOME',
      value: 100,
      comment: 'Income comment',
      id: 1
      },
        2: {
        type: 'OUTCOME',
        value: 50,
        sign: '-',
        comment: 'Outcome comment',
        id: 2
      }
    },
    result: 'all'
  }),
  methods: {
    deleteItem (id) {
      this.$delete(this.list, id)
    },

    onFormSubmit (data) {
      const newObj = {
        ...data,
        id: String(Math.random())
      }
      this.$set(this.list, newObj.id, newObj)
    },
    onButtonClick(res, e) {
      this.result = e.target.value
      console.log(res)

    }
  },
  computed: {
    totalValue () {
      return Object.values(this.list).reduce((acc,item) => {
        if (item.type === 'OUTCOME') {
          acc -= item.value
          return acc
        }
        acc += item.value
        return acc
      } ,0)
    },
  },

}
</script>

