<template>
  <ElCard class="form-card">
      <ElForm :model='formData' :rules='rules' ref="addItemForm" lable-position='top'>
        <ElFormItem label='Type' prop='type'>
          <ElSelect v-model="formData.type" placeholder='Choose type...' class="type-select">
            <ElOption lable='Income' value='INCOME' />
            <ElOption lable='Outcome' value='OUTCOME' />
          </ElSelect>
        </ElFormItem>
        <ElFormItem label='Comments' prop='comment'>
          <ElInput v-model="formData.comment" placeholder='Enter your comment' />
        </ElFormItem>
        <ElFormItem label='Value' prop='value'>
          <ElInput v-model.number='formData.value' />
        </ElFormItem>
        <ElButton @click="onSubmit" type='primary'>Submit</ElButton>
      </ElForm>
  </ElCard>
</template>

<script>
export default {
  name: "Form",
  components: {},
  props: {},
  data: () => ({
    formData: {
      type: 'INCOME',
      comment: '',
      value: 0
    },
  rules: {
    type: [
      {
        required: true,
        message: 'Please select type',
        trigger: 'blur'
      }
    ],
    value: [
      {
        required: true,
        message: 'Please enter value',
        trigger: 'change'
      },
      {
        type: 'number',
        message: 'Value must be a number',
        trigger: 'change'
      }
    ],
    comment: [
      {
        required: true,
        message: 'Please enter comment',
        trigger: 'change'
      },
    ]
  }

  }),
  methods: {
    onSubmit () {
      this.$refs.addItemForm.validate((valid) => {
         if(valid) {
           this.$emit('sumbitForm', {...this.formData})
           this.$refs.addItemForm.resetFields()
         }
      })
    }
  },
  computed: {}
}
</script>

<style scoped>
  .form-card {
    width: 700px;
    margin: 0 auto;
  }
  .type-select {
    width: 100%;
  }
</style>