<template>
  <div id='budget-list-wrap'>
      <ElCard :header='title'>
        <template v-if="!isEmpty">

          <template v-if="checkedValue === 'income'">
            <BudgetListItem v-for='(item, prop) in filterIncomeValue' :key="prop" :item='item' @onDelete='onDeleteItem(prop)'/>
          </template>

          <template v-else-if="checkedValue === 'outcome'">
            <BudgetListItem v-for='(item, prop) in filterOutcomeValue' :key="prop" :item='item' @onDelete='onDeleteItem(prop)'/>
          </template>

          <template v-else>
            <BudgetListItem v-for='(item, prop) in list' :key="prop" :item='item' @onDelete='onDeleteItem(prop)'/>
          </template>

        </template>
        <ElAlert  type='info' v-else :title='msgEmptyList' :closable='false' />
      </ElCard>
  </div>
</template>

<script>

import BudgetListItem from './BudgetListItem'

export default {
  name: 'BudgetList',
  components: {
    BudgetListItem
  },
  props: {
    list: {
       type: Object,
       default: () => ({})
    },
    checkedValue: {
      type: String,
      default: () => {'all'}
    }
  },
  data: () => ({
    title: 'Budget List',
    msgEmptyList: 'List is empty'
  }),
  methods: {
    onDeleteItem (id) {
      this.$emit('onDeleteItem', id)
    },
    onClick() {
      console.log('test')
      console.log(this.checkedValue)
    }
  },
  computed: {
    isEmpty() {
      return !Object.values(this.list).length
    },
    filterIncomeValue () {
      return Object.values(this.list).filter(item => item.type === 'INCOME')
    },
    filterOutcomeValue () {
      return Object.values(this.list).filter(item => item.type === 'OUTCOME')
    }
  }
}
</script>

<style scoped>
  #budget-list-wrap {
    width: 700px;
    margin: 0 auto;
  }
  .list-item {
    display: flex;
    align-items: center;
    padding: 10px 15px;
  }
  .budget-value {
    font-weight: bold;
    margin-left: auto;
    margin-right: 20px;
  }
</style>