<template>
  <div class="total-value" :class="addColor">Balance: {{ total }}</div>
</template>

<script>
export default {
  name: "TotalBalance",
  components: {},
  props: {
    total: {
      type: Number,
      default: 0
    },
  },
  data: () => ({
    activeColor: ''
  }),
  computed: {
    addColor() {
      return this.total === 0 ? 'black' : this.total < 0 ? 'red' : 'green'
    }
  }
}
</script>

<style scoped>
  .total-value {
    font-size: 26px;
    text-transform: uppercase;
    padding: 20px;
    text-align: center;
  }
  .red {
    color: red
  }
  .black {
    color: black
  }
  .green {
    color: green;
  }
</style>